@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@400;600&display=swap');

.row {
	display: flex;
	flex-direction: column;
	align-items: center;
}
.col-img {
	width: 50%;
	display: flex;
	justify-content: center;
	align-items: center;
}
@media (max-width: 992px) {
	.col-img {
		width: 80%;
		display: flex;
		justify-content: center;
		align-items: center;
	}
}

.responsive{
    width: 100%;
    height: auto;
    
}
.col-content {
	width: 100%;
	padding: 0 6rem 3rem;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: flex-end;
	order: -1;
}
@media (min-width: 992px) {
	.row {
		flex-direction: row;

		justify-content: space-between;
	}
	.col-content {
		width: 50%;
		width: 100%;
		padding: 0 6rem;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: flex-end;
		order: -1;
		margin-bottom: 2rem;
		
	}
	.col-content:nth-child(even) {
		order: 2;
	}
}
@media (max-width: 1024px) {
	.col-content {
		padding: 0 2rem;
		margin-bottom: 1rem;

	}
	.col-content:nth-child(even) {
		order: -1;
	}
}




.logo-container {
	position: relative;
}

.logo-absolute {
	position: absolute;
	top: 50%;
	left: 50%;

	transform: translate(-50%, -50%);
}

.text {
	padding:0 8rem;
}
@media (max-width: 480px) {
	.text {
		padding:0 4rem;
	}
}


.h3{
	font-weight: 600;
	font-size: 3.5vh;
	color:#21405A;
	margin-bottom: 1rem;
	
}

@media (max-width: 480px) {
	.h3{
		font-weight: 600;
		font-size: 3vh;
		color:#21405A;
		margin-top:1rem;
		
	}
}

.title{
	font-weight: 600;
	font-size: 5vh;
	color:#21405A;
	margin-bottom: 1rem;
	
}

.bold{
	font-weight: 600;
	font-size: 2vh;
	color:#21405A;
	margin-bottom: 1rem;
	margin-top: 1rem;
	
}


@media (max-width: 480px) {
	.title{
		font-weight: 600;
		font-size: 4vh;
		color:#21405A;
		
	}
}



.button {
	/* width:50%; */
	padding:0.5rem 0.5rem 0.5rem;
	font-size:2vh;
	border-radius: 0.5rem;
	background-color: #D8DF23;
  	border: none;
 	color:#21405A;
	font-weight: 600;
}

.button:hover {
	transition: all 0.2s ease-in-out;
	background:#21405A;
	color: white;
	cursor: pointer;
  }

.p {
	width:80%;
	text-align: end;
	font-size: 2vh;
	color:#21405A;
}
@media (max-width: 800px) {
	.p{
		text-align: justify;
		width:100%;
		
	}
}

.subtitle {
	text-align: end;
	font-size: 1.5vh;
	color:#21405A;
	font-weight: 800;
	margin-bottom: 0.5rem;
}
@media (max-width: 1180px) {
	.subtitle{
		margin-top:1rem;
		font-size: 1.5vh;
		margin-bottom: 1rem;
		
	}
}

.boldTitle{
	font-weight: 1000;
	font-size: 5vh;
	color:#21405A;
	margin-bottom: 1rem;

}
@media (max-width: 480px) {
	.boldTitle{
		font-weight: 1000;
		font-size: 4vh;
		color:#21405A;
		margin-top:0.5rem;
		
	}
}


.ha{
	font-weight: 600;
	font-size: 3.5vh;
	color:#21405A;
	margin-bottom: 0rem;
	
}
@media (max-width: 480px) {
	.ha{
		font-size: 3vh;
		
		
	}
}


@media (max-width: 480px) {
	.h3{
		font-weight: 600;
		font-size: 3vh;
		color:#21405A;
		margin-top:0.5rem;
		
	}
}

.logo{
    width: 3rem;
    height: auto;
	max-width:100%;
    
}
@media (max-width: 1180px) {
	.logo{
		
		margin-left:0.5rem;
		
	}
}


.items {
	margin-top:0.1rem;
	display: flex;
	flex-direction: row;
	align-items: center;
}
.col-list{
	display: block;
	flex-direction: column;
	justify-content: center;
	align-items: flex-end;
	order: -1;
}

@media (min-width: 992px) {
	.items {
		flex-direction: row;
		margin-bottom: 1rem;

		justify-content: space-between;
	}
	.col-list {
		width: 50%;
		width: 100%;
		display: block;
		
	}
	.col-content:nth-child(even) {
		order: 2;
	}
}

.textlist {
	width:70%;
	text-align: end;
	font-size: 2vh;
	color:#21405A;
	font-weight: 400;
	margin-bottom: 0.5rem;
	margin-left:12.5rem;
}
@media (max-width: 1180px) {
	.textlist{
		width:100%;
		margin-top:1rem;
		font-size: 2vh;
		margin-bottom: 1rem;
		margin-left:0rem;
		
	}
}